export default {
  namespaced: true,
  state: {
    user:null,
  },
  mutations: {
    SET_USER: function (state, payload) {
      state.user = {...payload}
    },
    UPDATE_USER_AFTERMATCH: function(state, payload){
      state.user.coins = payload.coins
      state.user.season = payload.season
    } 
  },
  getters: {
    getUserData: (state) => {
      return state.user
    },
    ranked: (state) => {
      return { elo: state.user.elo, id: state.user.id }
    },
  },
  actions: {
  },
}